import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';
declare var paypal

@IonicPage()
@Component({
  selector: 'page-paypal-checkout',
  templateUrl: 'paypal-checkout.html',
})
export class PaypalCheckoutPage {

  public amount: number = this.navParams.get('amount')

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private toast: ToastController
    ) {
  }

  ionViewDidLoad() {

    if (!this.amount) this.viewCtrl.dismiss()
    console.log(this.amount);
    const _amount = this.amount
    var _this = this

    paypal.Button.render({
      env: 'production',
      commit: true,
      style: {
        color: 'gold',
        size: 'small'
      },
      client: {
        sandbox: 'AXD_aOCopqUyduDmDGWXJ0HbBhquAANCu37LEt3yXn40T3gU0ns-12nDt-zdvB4tIUSYBembxZ84UcSC',
        production: 'AWHs7gj9JnaTbF9LIhZOjC7lQ_uH-adbN4zWhVfFzA_zy7EpLz091PjEsGzQSjaEaQuCDHOxe7GMvK5L'
      },
      payment: function(data, actions) {
        return actions.payment.create({
          payment: {
              transactions: [
                  {
                      amount: { total: _amount, currency: 'MXN' }
                  }
              ]
          }
        })
      },
      onAuthorize: function(data, actions) {
        return actions.payment.execute().then(async function(payment) {
          if (payment.state == 'approved') {
            _this.handlePaypalApproved(payment)
          }
        });
      },
    }, '#paypal-button')

  }

  handlePaypalApproved(payment: any) {
    this.toast.create({message: 'Pago realizado con PayPal correctamente.', duration: 2000}).present()
    this.viewCtrl.dismiss(payment)
  }

}



